body {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
}

.container {
    border: 2px solid green;
    position: relative;
    top: 4pc;
    border-radius: 10px;
    width: 20pc;
    height: 30pc;
    padding: 0;
    margin: 0;
}

.header-calc {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 1.5pc;
    font-stretch: wider;
    font-weight: lighter;
    text-transform: uppercase;
    text-align: center;
    margin-top: 15px;
    color: green;
    cursor: default;
}

.input {
    margin: auto;
    display: flex;
    position: relative;
    width: 90%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bolder;
    text-align: right;
    border: 2px solid green;
    font-size: 1.7pc;
    height: 50px;
    border-radius: 5px;
    color: #120063;
    padding-right: 0;
    background-color: white;
}

.btns {
    width: 93%;
    padding: 5px;
    position: relative;
    grid-gap: 10px;
    margin: auto;
    top: 20px;
    display: grid;
    grid-template-columns: auto;
    grid-auto-flow: column;
    justify-content: center;
}

.btn {
    font-size: 30px;
    color: #120063;
    cursor: pointer;
    border: 2px solid green;
    border-radius: 5px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    width: 4.1pc;
    height: 3pc;
    background-color: white;
    cursor: default;
}

.btn:hover {
    background-color: green;
    color: white;
}

.bottom {
    display: flex;
    position: relative;
    justify-content: center;
    margin-top: 40px;
    font-size: 11px;
    color: grey;
    cursor: default;
}